









import Vue from "vue";
import UiGrid from "../grid/index.vue";
import * as config from "../../config";

export default Vue.extend({
  name: "ThemeProvider",
  components: {
    UiGrid
  },
  props: {
    mode: {
      type: String,
      required: false,
      default: "light",
      validator(val: string): boolean {
        return ["light", "dark"].includes(val);
      }
    }
  },
  data() {
    return {
      ...config
    };
  }
});
